<template>
  <section class="welcome-screen">
  </section>
</template>

<script>
  import {mapGetters} from "vuex";

  export default {
    name: 'AuthHandle',
    components: {
    },
    computed: {
      ...mapGetters({
        memorials: 'dashboard/memorials',
      })
    },
    async mounted() {
        window.localStorage.removeItem('Eternity');
        await this.$store.dispatch('auth/login');

        if(!this.$store.getters['auth/isLoggedIn'](['customer'])) {
          await this.$router.push({ name: 'home' });
        }
        else {
          await this.$store.dispatch('dashboard/memorials');

          let redirect = null;

          if(localStorage.getItem('redirectUrl')) {
            redirect = localStorage.getItem('redirectUrl');
            localStorage.removeItem('redirectUrl');
          }
          else if(!this.memorials.length) {
            let routeData = this.$router.resolve({name: 'memorials.create.welcome'});
            redirect = window.location.origin+routeData.href;
          }
          else {
            let routeData = this.$router.resolve({name: 'dashboard.show'});
            redirect = window.location.origin+routeData.href;
          }

          window.location.href = redirect;
        }
    }
  }
</script>

